* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list,
.country-info {
  display:flex;
  flex-direction: column;
justify-content: flex-start;
  list-style: none;
}
.country {
  height:50px;
  display: flex;
  margin:10px;
  font-size: 25px;
  align-items: center;
}
.country > img {
  height:100%;
  width:80px;
  margin-right:15px;
}

.country-info span {
font-weight: bold;
}
